@import 'common';

.flashcards {
  list-style-type: none;
  padding: 0;
}
.flashcards li {
  background : $primary-color;
  padding: .5em 1em;
  margin-bottom: 1px;

  display: flex;
  justify-content: space-between;
}
.flashcards li:hover {
  opacity: .8;
  cursor: pointer;
}
.deleteAllRow {
  margin-bottom: 1em;
}
.deleteAllRow button { margin-right: 1em; }